.personalData {
    display: flex;
    justify-content: center;
    min-height: 100vh;
    width: 100%;
    &Container {
        display: flex;
        flex-direction: column;
        align-items: center;

        h1 {
            font-weight: 400;
            font-size: 26px;
            color: #424242;
        }
    }
    &TitleBox {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;

        width: 100%;
        height: 100px;
    }

    & ul li {
        text-decoration: none;
        list-style: none;
    }
}