.reviews {
    display: flex;
    width: 100vw;
    justify-content: center;
    &Container {
        display: flex;
        flex-direction: column;
        width: 80vw;
        align-items: center;
        justify-content: space-between;

        h1 {
            font-weight: 400;
            font-size: 26px;
            color: #424242;
        }
    }
    &TitleBox {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;

        width: 100%;
        height: 100px;
    }
    &EnterBox {
        display: flex;
        flex-direction: column;
        height: 25vh;
        width: 60%;
        justify-content: center;
        align-items: center;
        font-size: 21px;
        font-weight: 600;
    }
}