.footer {
    display: flex;
    flex-direction: row;
    justify-content: center;

    grid-template: footer;

    background-color: #010101;
    border-top: 1px solid black;
    padding: 20px 0 0 0;

    &Container {
        display: flex;
        flex-direction: column;
        align-items: center;
        color: white;
    }

    &AuthorBox {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;

        width: 100%;
        height: 70px;

        color: white;
    }

    &LinkBox {
        display: flex;
        flex-direction: column;
        align-items: flex-start;

        width: 100%;

        color: white;

        h1 {
            font-size: 26px;
        }
    }
}